.home{
    color: white;
    text-decoration: none;
}

.leftmenu{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color:white;
    position:relative;
}

.leftmenufirst{
    border-radius: 6px;
    width:270px;
    height:24vh;
    background-color: #151515;
    margin-top: 10px;
}

.leftmenulibrary{
    border-radius: 6px;
    color:white;
    width:270px;
    height:85vh;
    background-color: #151515;
    /* background-color: white; */
    margin-top: 10px;
}

button{
    color:black;
    background-color: white;
    border-radius: 60px;
    margin-left: 27px;
    margin-top: 1.5vh;
}

#fa-houseicon,.fa-brands, #homemenulibrary{
    color:white;
    margin-left: 27px;
    margin-top: 3vh;
}
.fa-solid{
    margin-right: 15px;
}

.leftmenulibrarycreate-1,.leftmenulibrarycreate-2,.leftmenulibrarycreate-3, .leftmenulibrarycreate-4{
    background-color: #212121;
    border-radius: 6px;
    margin: 3vh 0.5vw;
}

#leftmenulibrarytext1,#leftmenulibrarytext2,#leftmenulibrarytext3, #leftmenulibrarytext4{
    margin-left: 27px;
    margin-top: 27px;
}
